"use client";
import Link from 'next/link';
import { useState } from 'react';

const Footer = () => {
  const currentYear = new Date().getFullYear();
  const [email, setEmail] = useState('');
  const [isSubscribed, setIsSubscribed] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');
  const [contactFormOpen, setContactFormOpen] = useState(false);
  
  const handleNewsletterSubmit = async (e) => {
    e.preventDefault();
    if (!email || !email.includes('@')) return;
    setIsLoading(true);
    setError('');
    // Simulate async subscribe
    setTimeout(() => {
      setIsSubscribed(true);
      setEmail('');
      setIsLoading(false);
      setTimeout(() => setIsSubscribed(false), 3000);
    }, 800);
  };
  
  const footerSections = [
    {
      title: "Company",
      links: [
        { name: "Home", href: "https://enabledtalent.com" },
        { name: "About us", href: "https://enabledtalent.com/about-us" },
        { name: "Careers", href: "https://enabledtalent.com/careers" },
        { name: "Contact Us", href: "https://enabledtalent.com/contact" },
      ]
    },
    {
      title: "Services",
      links: [
        { name: "For Employers", href: "https://jobs.enabledtalent.com/job" },
        { name: "For Talents", href: "https://jobs.enabledtalent.com/job" },
      ]
    },
    {
      title: "Legal",
      links: [
        { name: "Privacy Policy", href: "https://enabledtalent.com/privacy-policy" },
      ]
    }
  ];

  return (
    <footer className="bg-[#182434] rounded-t-2xl md:rounded-t-4xl text-[#CD7F32]">
      <div className="max-w-[1100px] mx-auto px-4">
        {/* Newsletter Section */}
        <div className="py-8 md:py-12 border-b border-gray-700">
          <div className="max-w-2xl mx-auto text-center">
            <h2 className="text-2xl md:text-3xl font-semibold text-white mb-4">
              Stay Updated
            </h2>
            <p className="text-gray-300 mb-6 text-sm md:text-base">
              Subscribe to our newsletter for the latest updates on inclusive hiring and talent acquisition.
            </p>
            
            {isSubscribed ? (
              <div className="bg-green-500/10 border border-green-500/20 rounded-lg p-4 mb-4">
                <p className="text-green-400 font-medium">
                  âœ“ Thank you for subscribing! We'll keep you updated.
                </p>
              </div>
            ) : (
              <div>
                <form onSubmit={handleNewsletterSubmit} className="flex flex-col sm:flex-row gap-3 max-w-md mx-auto">
                  <input
                    type="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    placeholder="Enter your email address"
                    className="flex-1 px-4 py-3 rounded-lg bg-gray-800 border border-gray-600 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent text-sm"
                    required
                    disabled={isLoading}
                  />
                  <button
                    type="submit"
                    disabled={isLoading}
                    className="px-6 py-3 bg-[#CD7F32] hover:bg-[#b97a2e] disabled:bg-orange-400 disabled:cursor-not-allowed text-white font-medium rounded-lg transition-colors whitespace-nowrap text-sm focus:outline-none focus:ring-2 focus:ring-orange-500 focus:ring-offset-2 focus:ring-offset-gray-800"
                  >
                    {isLoading ? 'Subscribing...' : 'Subscribe'}
                  </button>
                </form>
                
                {error && (
                  <div className="bg-red-500/10 border border-red-500/20 rounded-lg p-3 mt-4">
                    <p className="text-red-400 text-sm font-medium">
                      {error}
                    </p>
                  </div>
                )}
              </div>
            )}
            
            <p className="text-xs text-gray-400 mt-4">
              We respect your privacy. Unsubscribe at any time.
            </p>
          </div>
        </div>

        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-12 gap-8 md:gap-12 py-6 md:py-8">
          {/* Logo column */}
          <div className="lg:col-span-3 flex justify-center sm:justify-start">
            <Link href="/" className="flex-shrink-0">
              <div className="flex items-center">
                <img 
                  src="/images/nav/logo-white.svg" 
                  alt="Enabled Talent Logo" 
                  className="w-auto sm:w-32 md:w-40 mt-[-10px] sm:mt-[-30px] md:mt-[2px]" 
                />
              </div>
            </Link>
          </div>
          
          {/* Links columns */}
          <div className="lg:col-span-6">
            <div className="grid grid-cols-1 sm:grid-cols-3 gap-6 md:gap-8">
              {footerSections.map((section, sectionIndex) => (
                <div key={sectionIndex} className="flex flex-col items-center sm:items-start">
                  <h3 className="text-sm font-medium mb-4">{section.title}</h3>
                  <ul className="space-y-3">
                    {section.links.map((link, linkIndex) => (
                      <li key={linkIndex} className="text-center sm:text-left">
                        <a 
                          href={link.href}
                          target={link.href && link.href.startsWith('https://') ? '_blank' : undefined}
                          rel={link.href && link.href.startsWith('https://') ? 'noopener noreferrer' : undefined}
                          className="text-sm text-gray-300 hover:text-white transition-colors"
                        >
                          {link.name}
                        </a>
                      </li>
                    ))}
                  </ul>
                </div>
              ))}
            </div>
          </div>
          
          {/* Social links column */}
          <div className="lg:col-span-3 flex flex-col items-center sm:items-start lg:items-end">
            <h3 className="text-sm font-medium mb-4">Follow us</h3>
            <div className="flex space-x-4">
              
              <Link 
                href="https://www.linkedin.com/company/enabledtalent/" 
                target="_blank" 
                rel="noopener noreferrer"
                className="bg-[#CD7F32] rounded-full p-2 text-white hover:bg-[#b97a2e] transition-colors"
              >
                <span className="sr-only">LinkedIn</span>
                <svg className="h-5 w-5" fill="currentColor" viewBox="0 0 24 24" aria-hidden="true">
                  <path d="M19 0h-14c-2.761 0-5 2.239-5 5v14c0 2.761 2.239 5 5 5h14c2.762 0 5-2.239 5-5v-14c0-2.761-2.238-5-5-5zm-11 19h-3v-11h3v11zm-1.5-12.268c-.966 0-1.75-.79-1.75-1.764s.784-1.764 1.75-1.764 1.75.79 1.75 1.764-.783 1.764-1.75 1.764zm13.5 12.268h-3v-5.604c0-3.368-4-3.113-4 0v5.604h-3v-11h3v1.765c1.396-2.586 7-2.777 7 2.476v6.759z"/>
                </svg>
              </Link>
              <Link 
                href="https://www.instagram.com/enabledtalent/" 
                target="_blank" 
                rel="noopener noreferrer"
                className="bg-[#CD7F32] rounded-full p-2 text-white hover:bg-[#b97a2e] transition-colors"
              >
                <span className="sr-only">Instagram</span>
                <svg className="h-5 w-5" fill="currentColor" viewBox="0 0 24 24" aria-hidden="true">
                  <path fillRule="evenodd" d="M12.315 2c2.43 0 2.784.013 3.808.06 1.064.049 1.791.218 2.427.465a4.902 4.902 0 011.772 1.153 4.902 4.902 0 011.153 1.772c.247.636.416 1.363.465 2.427.048 1.067.06 1.407.06 4.123v.08c0 2.643-.012 2.987-.06 4.043-.049 1.064-.218 1.791-.465 2.427a4.902 4.902 0 01-1.153 1.772 4.902 4.902 0 01-1.772 1.153c-.636.247-1.363.416-2.427.465-1.067.048-1.407.06-4.123.06h-.08c-2.643 0-2.987-.012-4.043-.06-1.064-.049-1.791-.218-2.427-.465a4.902 4.902 0 01-1.772-1.153 4.902 4.902 0 01-1.153-1.772c-.247-.636-.416-1.363-.465-2.427-.047-1.024-.06-1.379-.06-3.808v-.63c0-2.43.013-2.784.06-3.808.049-1.064.218-1.791.465-2.427a4.902 4.902 0 011.153-1.772A4.902 4.902 0 015.45 2.525c.636-.247 1.363-.416 2.427-.465C8.901 2.013 9.256 2 11.685 2h.63zm-.081 1.802h-.468c-2.456 0-2.784.011-3.807.058-.975.045-1.504.207-1.857.344-.467.182-.8.398-1.15.748-.35.35-.566.683-.748 1.15-.137.353-.3.882-.344 1.857-.047 1.023-.058 1.351-.058 3.807v.468c0 2.456.011 2.784.058 3.807.045.975.207 1.504.344 1.857.182.466.399.8.748 1.15.35.35.683.566 1.15.748.353.137.882.3 1.857.344 1.054.048 1.37.058 4.041.058h.08c2.597 0 2.917-.01 3.96-.058.976-.045 1.505-.207 1.858-.344.466-.182.8-.398 1.15-.748.35-.35.566-.683.748-1.15.137-.353.3-.882.344-1.857.048-1.055.058-1.37.058-4.041v-.08c0-2.597-.01-2.917-.058-3.96-.045-.976-.207-1.505-.344-1.858a3.097 3.097 0 00-.748-1.15 3.098 3.098 0 00-1.15-.748c-.353-.137-.882-.3-1.857-.344-1.023-.047-1.351-.058-3.807-.058zM12 6.865a5.135 5.135 0 110 10.27 5.135 5.135 0 010-10.27zm0 1.802a3.333 3.333 0 100 6.666 3.333 3.333 0 000-6.666zm5.338-3.205a1.2 1.2 0 110 2.4 1.2 1.2 0 010-2.4z" clipRule="evenodd" />
                </svg>
              </Link>
              <Link 
                href="" 
                target="_blank" 
                rel="noopener noreferrer"
                className="bg-[#CD7F32] rounded-full p-2 text-white hover:bg-[#b97a2e] transition-colors"
              >
                <span className="sr-only">Facebook</span>
                <svg className="h-5 w-5" fill="currentColor" viewBox="0 0 24 24" aria-hidden="true">
                  <path fillRule="evenodd" d="M22 12c0-5.523-4.477-10-10-10S2 6.477 2 12c0 4.991 3.657 9.128 8.438 9.878v-6.987h-2.54V12h2.54V9.797c0-2.506 1.492-3.89 3.777-3.89 1.094 0 2.238.195 2.238.195v2.46h-1.26c-1.243 0-1.63.771-1.63 1.562V12h2.773l-.443 2.89h-2.33v6.988C18.343 21.128 22 16.991 22 12z" clipRule="evenodd" />
                </svg>
              </Link>

              {/* <Link 
                href="https://www.tiktok.com/@enabledtalent" 
                target="_blank" 
                rel="noopener noreferrer"
                className="bg-[#CD7F32] rounded-full p-2 text-white hover:bg-[#b97a2e] transition-colors"
              >
                <img src="/images/logos/tik-tok.png" alt="Facebook" className="w-5 h-5" />
              </Link> */}
            </div>
          </div>
        </div>
        
        <div className="mt-8 pt-6 border-t border-gray-800 text-center text-xs text-white">
          <p>Â© {currentYear} Enabled Talent. All rights reserved.</p>
        </div>
      </div>
    </footer>
  );
};

export default Footer; 